<template>

    <div
        class="menu-container"
    >
        <label for="checkbox-show-menu" id="label-show-menu">?</label>
        <input type="checkbox" id="checkbox-show-menu" class="checkbox-with-label" v-model="menuState">
        <!-- ESCALA SAAATY -->
        <label
            class="label-menu-ahp"
            for="checkbox-mostra-escala-saaty"
            v-if="tabView !== 'resultados' && menuState"
        >
            {{ $t('helpEscalaSaaty') }}
        </label>
        <input
            type="checkbox"
            class="checkbox-menu-ahp"
            id="checkbox-mostra-escala-saaty"
            v-model="mostrarEscala"
        >
        <vueEscalaSaaty
            class="escala"
            v-if="mostrarEscala  && menuState"
        />
        <!-- HELP PRIMEIRA ETAPA -->
        <label
            class="label-menu-ahp"
            v-if="tabView === 'primeira'  && menuState"
            for="button-menu-primeira-etapa"
        >
            {{ $t('helpPrimeiraEtapa') }}
        </label>
        <button
            class="button-menu-ahp"
            id="button-menu-primeira-etapa"
            @click="abrirPopUp('helpPrimeiraEtapa')"
        >
        </button>
        <!-- HELP SEGUNDA ETAPA -->
        <label
            class="label-menu-ahp"
            v-if="tabView === 'segunda' && menuState"
            for="button-menu-segunda-etapa"
        >
            {{ $t('helpSegundaEtapa') }}
        </label>
        <button
            class="button-menu-ahp"
            id="button-menu-segunda-etapa"
            @click="abrirPopUp('helpSegundaEtapa') && menuState"
        ></button>
        <!-- MENU RESULTADOS ETAPA -->
        <label
            class="label-menu-ahp"
            v-if="tabView === 'resultados' && menuState"
            for="button-menu-resultados-etapa"
        >
            {{ $t('helpResultadosEtapa') }}
        </label>
        <button
            class="button-menu-ahp"
            id="button-menu-resultados-etapa"
            @click="abrirPopUp('helpResultadosEtapa')"
        ></button>
        <!-- PASSO A PASSO AHP -->
        <label
            class="label-menu-ahp"
            for="button-calculos-ahp"
            v-if="menuState"
        >
            {{ $t('helpCalculosAhp') }}
        </label>
        <button
            class="button-menu-ahp"
            id="button-calculos-ahp"
            @click="abrirPopUp('passosAhp')"
        >
        </button>
        <!-- MAIS SOBRE AHP-->
        <label
            class="label-menu-ahp"
            for="button-mais-sobre-ahp"
            v-if="menuState"
        >
            {{ $t('helpMaisSobreAhp') }}
        </label>
        <button
            class="button-menu-ahp"
            id="button-mais-sobre-ahp"
            @click="abrirPopUp('maisSobreAhp')"
        >
        </button>

    </div>

</template>
<script>
import vueEscalaSaaty from  "@/components/escalaSaaty.vue"

export default{
    name: "vue-escala-saaty",
    components:{
        vueEscalaSaaty
    },
    data() {
        return{
            mostrarEscala:false,
            mostrarHelpPrimeiraEtapa:false,
            mostrarHelpSegundaEtapa:false,
            mostrarHelpResultadosEtapa:false,
            menuState: false
        }
    },
    computed:{
        tabView() {
            return this.$store.getters.currentTabViewAtual
        }
    },
    watch:{
        menuState() {
            console.log(this.menuState)
        }
    },
    methods:{
        abrirPopUp(frame) {
            this.$store.dispatch("changePopUp", frame)
        }
    }

}
</script>
<style scoped>
    label{
        background-color: white;
    }
    .menu-container {
        position: fixed;
        margin-top: 20px;
        right: 2%;
        display: flex;
        flex-direction: column;
    }

    .label-menu-ahp {
    border: var(--borda-simples);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    font-size: 10pt;
    font-weight: bold;
    position: relative;
    align-self: flex-end;
    }

    /* Correção aqui: */
    .label-menu-ahp:hover {
    cursor: pointer;
    box-shadow: 0 0 10px var(--cor-hover);
    transform: scale(1.1);
    }

    .checkbox-menu-ahp {
    display: none;
    }
    .button-menu-ahp{
    display: none;
    }
    #label-show-menu{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: var(--borda-simples);
        font-weight: bold;
        font-size: 12pt;
        text-align: center;
        background-color: white;
        justify-self: end;
        align-self: end;
    }
    #label-show-menu:hover{
        transform: scale(1.2);
        cursor: pointer;
    }
    .checkbox-with-label{
        display: none;
    }
</style>
